@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');
body{
  margin: 0;
  padding:0;
  font-family: 'Lora', serif;
  background-color: aqua;
}
*{
  box-sizing: border-box;
}
.todo{
  display: flex;
  flex-direction: column;
  margin: 100px auto;
  max-width: 350px;
  border-radius: 8%;
  min-height: 400px;
  padding: 20px;
  background-color: antiquewhite;
  color: rgb(40, 136, 123);
}
.todo-item{
  display: flex;
  justify-content: flex-start;
}
.ml-1{
  margin-left: 10px;
}
form{
  display: flex;
  width: 100%;
  margin: 20px auto;
  justify-content: space-between;
}
form input{
  width: 85%;
  padding: 7px;
  border: none;
  border-bottom: 2px solid white;
  outline: none;
}
::placeholder{
  font-size: 15px;
  letter-spacing: 2px;
  color: #888  ;
}
form button{
  background-color: transparent;
  border: 1px solid #999;
  width:13%;
  cursor: pointer;
  border-radius: 6px;
  padding: 7px;
}
button:hover{
  background-color: black;
  border-color: black;
  color: white;
}